import React, { useEffect } from "react";
import { Link } from "react-router-dom";
import "./rules_style.css";

const Rules_page = ({ game_type }) => {
  useEffect(() => {
    if (game_type == "Single") {
      document.body.style.backgroundColor = "blue";
    } else {
      document.body.style.backgroundColor = "purple";
    }
  }, []);
  return (
    <div
      className={
        game_type == "Single" ? "rules_container" : "m_rules_container"
      }
    >
      <div className="info_box">
        <div className="info-title">
          <span>Some Rules of this Quiz</span>
        </div>
        <div
          className={game_type == "Single" ? "info-list" : "info-list purple"}
        >
          <span className="info-number">1.</span>
          <div className="info">
            {game_type == "Single" ? (
              <p>
                You will have only <span>15 seconds</span> for each question.
              </p>
            ) : (
              <p>
                To host a game: Copy the Booking code then click on{" "}
                <span>Create Game</span> send the code to your partner"
              </p>
            )}
          </div>
          <span className="info-number">2.</span>
          <div className="info">
            {game_type == "Single" ? (
              "Once you select your answer, it can't be undone."
            ) : (
              <p>
                To Join a game: Click on <span>Join Game</span> then enter the
                code from partner
              </p>
            )}
          </div>
          <span className="info-number">3.</span>
          <div className="info">
            {game_type == "Single" ? (
              "You can't exit from the Quiz while you're playing."
            ) : (
              <p>
                You will have only <span>15 seconds</span> for each question.
              </p>
            )}
          </div>
          <span className="info-number">4.</span>
          <div className="info">
            {game_type == "Single"
              ? "You can't select any option once time goes off."
              : "You can only select an answer when its your turn to play. This will be indicated on the top left corner."}
          </div>
          <span className="info-number">5.</span>
          <div className="info">
            {game_type == "Single"
              ? "You'll get points on the basis of your correct answers."
              : "Try to select an option before the time runs out. So, you don't keep your partner waiting."}
          </div>
          <span></span>
          <p className="setCenter">Happy Gaming :)</p>
        </div>
        <div className="buttons">
          <a
            href="https://yos-quiz.herokuapp.com/"
            className={
              game_type == "Single"
                ? "quit btn red-color"
                : "btn purple-quit red-color"
            }
          >
            Exit Quiz
          </a>
          <Link
            to={game_type == "Single" ? "/quiz_page" : "/m_quiz"}
            className={
              game_type == "Single" ? "restart btn" : "btn purple-restart"
            }
          >
            Continue
          </Link>
        </div>
      </div>
    </div>
  );
};

export default Rules_page;
